.move div {
  width: 300px;
  height: 300px;
  display: inline-block; }

.move .move-margin {
  background: red;
  margin-right: -20px;
  margin-bottom: 20px; }

.move .move-transform {
  background: blue;
  -webkit-transform: translate(20px, -20px);
      -ms-transform: translate(20px, -20px);
          transform: translate(20px, -20px); }



